'use client';

import { ExtendedVehicleIssue } from '@/src/lib/vehicleIssues/vehicleIssueTypes';
import { Card, CardContent } from '@/src/components/ui/card';
import { Bug, ClipboardCheck, Loader2, Wrench, CheckCircle, Ban } from 'lucide-react';

interface Props {
  issues: ExtendedVehicleIssue[];
}

export default function VehicleIssuesSummaryCard({ issues }: Props) {
  const total = issues.length;
  const pending = issues.filter((i) => i.status === 'pending').length;
  const assigned = issues.filter((i) => i.status === 'assigned').length;
  const inProgress = issues.filter((i) => i.status === 'in_progress').length;
  const resolved = issues.filter((i) => i.status === 'resolved').length;
  const closed = issues.filter((i) => i.status === 'closed').length;

  const items = [
    {
      title: 'Total Issues',
      value: total,
      color: 'text-black',
      icon: <Bug className="w-6 h-6 text-gray-500" />,
      bg: 'bg-gray-100',
    },
    {
      title: 'Pending',
      value: pending,
      color: 'text-yellow-600',
      icon: <ClipboardCheck className="w-6 h-6 text-yellow-600" />,
      bg: 'bg-yellow-50',
    },
    {
      title: 'Assigned',
      value: assigned,
      color: 'text-blue-600',
      icon: <Wrench className="w-6 h-6 text-blue-600" />,
      bg: 'bg-blue-50',
    },
    {
      title: 'In Progress',
      value: inProgress,
      color: 'text-indigo-600',
      icon: <Loader2 className="w-6 h-6 text-indigo-600 animate-spin" />,
      bg: 'bg-indigo-50',
    },
    {
      title: 'Resolved',
      value: resolved,
      color: 'text-green-600',
      icon: <CheckCircle className="w-6 h-6 text-green-600" />,
      bg: 'bg-green-50',
    },
    {
      title: 'Closed',
      value: closed,
      color: 'text-gray-500',
      icon: <Ban className="w-6 h-6 text-gray-500" />,
      bg: 'bg-gray-100',
    },
  ];

  return (
    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-6 gap-4 mb-6">
      {items.map((item, idx) => (
        <Card key={idx} className={`shadow-sm ${item.bg}`}>
          <CardContent className="p-4 flex items-center justify-between">
            <div>
              <h2 className="text-sm text-gray-500 mb-1 font-medium uppercase tracking-wide">
                {item.title}
              </h2>
              <p className={`text-3xl font-bold ${item.color}`}>{item.value}</p>
            </div>
            <div className="ml-4">{item.icon}</div>
          </CardContent>
        </Card>
      ))}
    </div>
  );
}