import { Dispatch, SetStateAction } from 'react';

interface Props {
  searchTerm: string;
  setSearchTerm: Dispatch<SetStateAction<string>>;
  statusFilter: string;
  setStatusFilter: Dispatch<SetStateAction<string>>;
  stationFilter: string; // ‚úÖ b·ªï sung
  setStationFilter: Dispatch<SetStateAction<string>>; // ‚úÖ b·ªï sung
}

export default function VehicleIssuesSearchFilter({
  searchTerm,
  setSearchTerm,
  statusFilter,
  setStatusFilter,
  stationFilter,
  setStationFilter,
}: Props) {
  return (
    <div className="flex flex-col md:flex-row gap-4 items-center bg-white p-4 rounded-lg shadow">
      <input
        type="text"
        placeholder="Search by VIN, plate or description..."
        className="border rounded p-2 flex-1"
        value={searchTerm}
        onChange={(e) => setSearchTerm(e.target.value)}
      />
      <select
        className="border rounded p-2"
        value={statusFilter}
        onChange={(e) => setStatusFilter(e.target.value)}
      >
        <option value="All">All Statuses</option>
        <option value="pending">Pending</option>
        <option value="assigned">Assigned</option>
        <option value="proposed">Proposed</option>
        <option value="confirmed">Confirmed</option>
        <option value="rejected">Rejected</option>
        <option value="in_progress">In Progress</option>
        <option value="resolved">Resolved</option>
        <option value="closed">Closed</option>
      </select>

      <input
        type="text"
        placeholder="Filter by Station..."
        className="border rounded p-2"
        value={stationFilter}
        onChange={(e) => setStationFilter(e.target.value)}
      />
    </div>
  );
}
